<template>
  <div @click="isVisibleDialog = true" class="exercise-element-list" ref="card">
    <img
      :class="['programming-icon',hardness]"
      :src="iconLanguage"
      :title="`${exercise.language.name} ${$t('components.exerciseElementList.difficult')} ${hardness}`"
    >

    <div class="content">
      <div class="name" :title="exercise.name">{{ exercise.name }}</div>

      <div class="middle-info">
        <div class="date" :title="$t('components.exerciseElementList.date')">
          <md-icon>access_time</md-icon>
          {{ new Date(exercise.creationDate).toLocaleDateString("en-US") }}
        </div>
        <div class="author" :title="$t('components.exerciseElementList.author')">
          <md-icon>account_circle</md-icon>
          {{ exercise.author.username }}
        </div>
      </div>

      <div class="calification" :title="$t('components.exerciseElementList.calification')">
        <md-icon>rate_review</md-icon>
        <stars-rating :config="config"/>
      </div>
    </div>

    <md-icon v-if="!exercise.isPublic" class="lock">lock_outline</md-icon>

    <exercise-dialog v-model="isVisibleDialog" :exercise="exercise"></exercise-dialog>
  </div>
</template>


<script>
import starsRating from "@/components/stars-rating";
import exerciseDialog from "@/components/exercise-dialog";

export default {
  name: "exercise-element-list",
  components: {
    starsRating,
    exerciseDialog
  },
  props: {
    exercise: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      iconLanguage:
        "./img/program-icons/" + this.exercise.language.name + ".svg",
      hardness: "low",
      config: {
        rating: this.exercise.valoration / 2,
        isIndicatorActive: false,
        style: {
          fullStarColor: "#ffd700",
          emptyStarColor: "#ddd",
          starWidth: 15,
          starHeight: 15
        }
      },
      isVisibleDialog: false
    };
  },
  methods: {
    // TODO: Ask change in server numbers -> words
    hardnessEval() {
      var dif = this.exercise.hardness;
      if (dif >= 4) {
        this.hardness = "hard";
      } else if (dif == 3) {
        this.hardness = "medium";
      } else {
        this.hardness = "low";
      }
    }
  },
  created() {
    this.hardnessEval();
  }
};
</script>

<style lang="scss" scoped>
.exercise-element-list {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  min-height: 50px;
  cursor: pointer;

  .programming-icon {
    width: 2.5em;
    height: 2.5em;
    margin-right: 0.5em;
    padding: 0.3em;
    border: 3px solid#716de8;
    border-radius: 2em;

    &.hard {
      border-color: #bd0052;
    }
    &.medium {
      border-color: #fb8b40;
    }
    &.low {
      border-color: #84b84c;
    }
  }
  .content {
    color: grey;

    .name {
      font-size: 0.9rem;
      margin: 2%;
      /* Line clamp */
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
    }
    .middle-info {
      display: flex;
      flex-direction: row;

      .md-icon {
        font-size: 1.3em !important;
        color: lightgrey;
      }
      .date {
        color: lightgrey;
        font-size: 0.8rem;
      }
      .author {
        color: lightgrey;
        font-size: 0.8rem;
      }
    }
    .calification {
      display: flex;
      flex-direction: row;

      .md-icon {
        color: grey;
        font-size: 1rem !important;
      }
    }
  }

  .lock {
    position: absolute;
    bottom: 0px;
    right: 0px;
    display: flex;
    flex-direction: row;
    font-size: 1em !important;
    padding: 0.2em;
    color: grey;
    background-color: lightgrey;
    border-radius: 2em;
  }
}
</style>
