<template>
  <div class="list-tile-ar" :style="{ 'border': '1px solid ' + color}">
    <div class="header" :style="{ 'background-color': color }">
      <md-icon>{{ icon }}</md-icon>
      <div class="title">{{ title }}</div>
    </div>
    <div class="list">
      <slot>
        <div class="empty">{{ etext }}</div>
      </slot>
    </div>
    <div class="footer">
       <slot name="footer"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "list-tile-ar",
  props: {
    title: {
      default: "MOST VIEWED",
      type: String
    },
    etext: {
      default: "Is Empty",
      type: String
    },
    icon: {
      default: "school",
      type: String
    },
    color: {
      default: "#e8716d",
      type: String
    }
  },
};
</script>

<style lang="scss" scoped>
.list-tile-ar {
  position: relative;
  min-width: 300px;
  padding-bottom: 4em;
  background-color: #eaeaea;
  border-radius: 5px;

  .header {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    height: 4em;
    padding: 5px;
    overflow: hidden;
    border-radius: 5px 5px 0px 0px;

    .md-icon {
      width: 40px;
      height: 40px;
      margin-left: 5px;
      padding: 15px;
      background-color: rgba(0,0,0,0.1);
      border-radius: 2em;
      color: white;
    }
    .title {
      width: 80%;
      margin-left: 10px;
      font-weight: bold;
      color: white;
      font-size: 1em;
    }
  }

  .list {
    .empty {
      margin-top: 35px;
      margin-bottom: 20px;
      text-align: center;
      font-weight: bold;
      color: lightgrey;
    }
  }

  .footer a {
    position: absolute;
    bottom: 10px;
    right: 10px;
    padding: 5px 10px;
    background-color: white;
    color: grey;
    text-decoration: none;
    cursor: pointer;
    transition: box-shadow 1s;
  }
  a:hover {
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25);
  }
}
</style>