<style lang="scss" scoped>
.md-dialog {
  background-color: #ebeced;
  max-width: 500px;
  min-width: 350px;
  //-webkit-backface-visibility: hidden;
  //-webkit-transform: translateZ(0) scale(1, 1);

  .header {
    position: relative;
    width: 100%;
    height: 180px;

    .img {
      height: 60%;
      width: 100%;
      background: url("../assets/img/code-rain-compressor.jpg") no-repeat center;
      background-color: #cccccc;
      background-size: cover;
    }
    .title {
      position: relative;
      display: flex;
      flex-direction: row;
      align-items: center;
      width: 100%;
      height: 35%;
      padding: 0.5em;

      background-color: #e8716d;
      box-shadow: 0px -3px 15px black;

      span {
        color: white !important;
      }
      .programming-icon {
        padding: 0.5em;
        background-color: white;
        border-radius: 100px;
        border: 0.5em solid #e8716d;
        z-index: 10;
      }
      .md-title {
        margin-right: 40px;
      }
      .md-icon-button {
        position: absolute;
        top: 17%;
        right: 10px;

        .md-icon {
          color: #e8716d;
        }
      }
    }
  }

  .content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(6, auto);
    grid-gap: 5px 5px;

    padding: 5px;

    .tags {
      grid-column: 1 / 3;
      grid-row: 1;

      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: left;
      .md-chip {
        margin: 3px 3px;
      }
    }
    .desc {
      grid-column: 1 / 3;
      grid-row: 5;
    }

    .hardness,
    .valoration,
    .author,
    .validator,
    .language,
    .date {
      display: flex;
      align-items: center;
    }
    .card {
      padding: 0.3em;
      background-color: white;
      border: 1px solid lightgrey;
      span {
        font-weight: bold;
      }
    }
  }
  .md-dialog-actions {
    position: relative;
    bottom: 0;
    right: 0;
  }
}
</style>

<template>
  <div class="exercise-dialog">
    <md-dialog :md-active.sync="active" @md-clicked-outside="hideDialog" :md-fullscreen="false">
      <div class="header">
        <div class="img"></div>
        <div class="title">
          <img class="programming-icon" :src="iconLanguage">
          <span class="md-title">{{exercise.name}}</span>

          <md-button
            @click="$router.push({path:'/intranet/exercise/play', query: {exercise:exercise}})"
            class="md-icon-button md-raised"
            :title="$t('components.exerciseDialog.play')"
          >
            <md-icon>play_arrow</md-icon>
          </md-button>
        </div>
      </div>

      <md-dialog-content>
        <div class="content">
          <div class="tags">
            <md-chip v-for="(value, index) in tags" :key="index" class="md-primary">{{ value }}</md-chip>
          </div>

          <div class="card hardness">
            <span>{{$t('components.exerciseDialog.hardness')}}: </span>
            {{exercise.hardness}}
          </div>

          <div class="card valoration">
            <span>{{$t('components.exerciseDialog.valoration')}}: </span>
            {{exercise.valoration}}
          </div>

          <div class="card author">
            <span>{{$t('components.exerciseDialog.author')}}: </span>
            {{ exercise.author.username }}
          </div>

          <div class="card validator">
            <span>{{$t('components.exerciseDialog.validator')}}: </span>
            {{ exercise.validator.username }}
          </div>

          <div class="card language">
            <span>{{$t('components.exerciseDialog.language')}}: </span>
            {{ exercise.language.name }}
          </div>

          <div class="card date">
            <span>{{$t('components.exerciseDialog.creationDate')}}: </span>
            {{ new Date(exercise.creationDate).toLocaleDateString("en-US") }}
          </div>

          <div class="card desc">
            <span>{{$t('components.exerciseDialog.description')}}: </span>
            <br>
            {{ exercise.description }}
          </div>
        </div>
      </md-dialog-content>

      <md-dialog-actions>
        <md-button class="md-accent" @click="hideDialog">{{$t('components.exerciseDialog.close')}}</md-button>
      </md-dialog-actions>
    </md-dialog>
  </div>
</template>

<script>
export default {
  name: "exercise-dialog",
  props: {
    value: {
      // Open-Close dialog
      type: Boolean,
      default: false
    },
    exercise: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      active: this.value,
      iconLanguage:
        "./img/program-icons/" + this.exercise.language.name + ".svg",
      tags: []
    };
  },
  watch: {
    value: function() {
      this.active = this.value;
    }
  },
  created() {
    this.tagsToArray();
  },
  methods: {
    hideDialog() {
      this.active = false;
      this.$emit("input", this.active);
    },
    tagsToArray() {
      this.tags = this.exercise.tags.split(";");
    }
  }
};
</script>
