<template>
  <div :style="{'background-color': bkgColor}" class="svg-toolbar-ar">
    <div class="align-center">
      <!-- Must be a an svg with paths -->
      <slot class="code-logo"/>
      <h1 :style="{'color':fontColor}">{{ title }}</h1>
    </div>
  </div>
</template>

<script>
export default {
  name: "svg-toolbar-ar",
  props: {
    title: {
      type: String,
      required: true
    },
    bkgColor: {
      default: "#efde61",
      type: String
    },
    fontColor: {
      default: "#aa7f3d",
      type: String
    }
  }
};
</script>

<style lang="scss" scoped>
.svg-toolbar-ar {
  position: relative;
  height: 100px;
  width: 100%;
  padding: 10px;
  border-bottom-left-radius: 100% 50px;
  border-bottom-right-radius: 100% 50px;

  font-weight: 500;
  font-size: 1.2em;

  .align-center {
    display: flex;
    align-items: center;
    flex-direction: row;
    height: 100%;
    width: fit-content;
    margin: 0 auto;

    h1 {
      font-size: 2em;
      margin: 0.3em;
      opacity: 0;
      animation: fadein 1s ease-in forwards 1.5s;
    }
    svg {
      height: 80%;

      path {
        display: block;

        stroke-dasharray: 1000;
        stroke-dashoffset: 1000;
        stroke-width: 5px;
        stroke-miterlimit: 5;

        fill-opacity: 0;
        animation-timing-function: ease-in-out;
        animation-fill-mode: forwards;

        animation-name: DrawLine, FadeStroke, FillIn;
        animation-duration: 4s, 1s, 1s;
        animation-delay: 0s, 1.5s, 1.5s;
      }
    }
  }

  @keyframes fadein {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  @keyframes DrawLine {
    to {
      stroke-dashoffset: 0;
    }
  }

  @keyframes FadeStroke {
    to {
      stroke-opacity: 0;
    }
  }

  @keyframes FillIn {
    from {
      fill-opacity: 0;
    }
    to {
      fill-opacity: 1;
    }
  }
}
</style>
